
import { defineComponent, PropType } from "vue";
import { PwdBreadCrumbNode } from "../composition/pwd";
import LinkText from "./LinkText.vue";

export default defineComponent({
  components: { LinkText },
  emits: ["changeDir"],
  props: {
    nodes: {
      type: Object as PropType<PwdBreadCrumbNode[]>,
      require: true,
    },
  },
  setup(props, { emit }) {
    const cd = (dir: string) => {
      emit("changeDir", dir);
    };
    return {
      cd,
    };
  },
});
